import React, {useState} from "react";
import style from "../assets/scss/index.module.scss";
import classNames from "classnames";
import {Link} from "gatsby";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faBars, faTimes} from "@fortawesome/free-solid-svg-icons";
import Links from "./links";
import TagList from "./taglist";
import {Subject} from "rxjs";
import {delay} from "rxjs/operators";

enum openState {
    open,
    closing,
    close
}

export const Header: React.FunctionComponent = () => {
    const [isOpen, setIsOpen] = useState(openState.close);
    const subject: Subject<openState> = new Subject<openState>();
    subject.pipe(delay(900)).subscribe((data: openState) => {
        if (data === openState.closing) {
            setIsOpen(openState.close);
        }
    });

    const toggle = (): any => {
        subject.next(isOpen === openState.close ? openState.open : openState.closing)
        setIsOpen(isOpen === openState.close ? openState.open : openState.closing);
    };

    return <div className={classNames(style.uHeader,
        style.uHeaderSide,
        style.cNavbarSideLeft,
        style.animateAnimated,
        style.animateFast,
        {[style.animateFadeInDown]: isOpen === openState.open},
        {[style.animateFadeOutUp]: isOpen === openState.closing})}>
        <div className={classNames(style.uHeaderSectionsContainer, style.gBgWhite)}>
            <div className={classNames(style.uHeaderSection, style.uHeaderSectionLight)}>
                <nav className={classNames(style.navbar, style.navbarExpandLg)}>
                    <div className={classNames(style.container)}>
                        <div>
                            <Link to="/" className={classNames(style.navbarBrand, style.gMb40Lg)}>
                                Bruno Jan's Blogger
                            </Link>
                            <button onClick={toggle}
                                    className={classNames(style.navbarToggler,
                                        style.gMt5,
                                        style.btn,
                                        style.btnMd,
                                        style.gLineHeight1,
                                        style.gBrdNone,
                                        style.gPa0,
                                        style.gPosAbs,
                                        style.gTop3,
                                        style.gRight0,
                                        style.collapsed)}
                                    type="button">
                                <FontAwesomeIcon className={style.mr2} size={"lg"}
                                                 icon={isOpen === openState.open ? faTimes : faBars}/>
                            </button>
                            <div className={classNames(style.collapse,
                                style.navbarCollapse,
                                style.alignItemsCenter,
                                style.flexSmRow,
                                style.w100,
                                style.gMt20,
                                style.gMt0Lg,
                                style.gMb40,
                                {[style.show]: isOpen === openState.open || isOpen === openState.closing}
                            )}>
                                <ul className={classNames(style.navbarNav,
                                    style.mlAuto,
                                    style.textUppercase,
                                    style.gFontWeight600,
                                    style.uSubMenuV1)}>
                                    <Links/>
                                    <li className={classNames(style.navItem)}>
                                        <hr className={classNames(style.gBrdGrayLightV4)}/>
                                    </li>
                                    <TagList/>
                                </ul>
                            </div>
                        </div>
                    </div>
                </nav>
            </div>
        </div>
    </div>
}